<template>
    <div class="selectarea" v-if="areaStatus">
    
        <van-nav-bar            
            left-arrow
            title="选择地区"
            @click-left="onClickLeft"
        
        >
        </van-nav-bar>
        <section class="main" v-if="data" >
            <ul>
                <li v-for="(item,key) in data" :key="key">
                    <p class="title">{{key}}</p>
                    <ul>
                        <li v-for="(item_1,index) in item" :key="index" @click="handleClick(item_1)">
                            <p><span>{{item_1.name}}</span><van-icon name="arrow" /></p>
                        </li>
                    </ul>
                </li>
            </ul>
            <div class="item-area" v-if="itemData && show">
                <ul>
                    <li v-for="(item,key) in  itemData" :key="key" @click="handleCityClick(item)">
                        <p>{{item.name}}</p>
                    </li>
                </ul>
            </div>
        </section>
        <van-overlay :show="show" @click="show = false" />
    </div>
    
</template>

<script>
import { Toast } from 'vant';
export default {
    props:['areaStatus'],
    data(){
        return {
           show:false,
           upid:0,
           data:"",
           itemData:'',
           city:'',  // 城市
           province:'',  // 省份
        }
    },
    created(){
        this.getDistrict();
    },
    methods:{
        getDistrict(){
            Toast.loading({
                message: '加载中...',
                forbidClick: true
            });
            this.itemData = [];
            this.api.getDistrict({
                token:this.$store.state.userData,
                upid:this.upid
            }).then( res => {
                Toast.clear();
                if(res.err_code == 0){
                    if(this.upid == 0){
                        this.data = res.data
                    }else{
                        this.itemData = res.data
                    }
                    
                }
            })
        },
        // 省份点击事件
        handleClick(v){
            this.show = true;
            this.upid = v.id;
            this.province = v.name
            this.getDistrict();
        },
        // 城市点击事件
        handleCityClick(v){
           
            this.city = v.name;
            this.saveMyInfoByAddress();
        },
        // 编辑地区
        saveMyInfoByAddress(){
            Toast.loading({
                message: '加载中...',
                forbidClick: true
            });
            this.api.saveMyInfoByAddress({
                token:this.$store.state.userData,
                province:this.province,
                city:this.city
            }).then( res => {
                Toast.clear();
                this.show = false;
                if(res.err_code == 0){
                   this.$emit('getuserinfo',false)
                }else {
                    Toast(res.err_msg)
                }
            })
        },
        onClickLeft(){
            this.$emit('goAreaBack',false)
        }
    }
}
</script>
