                            display: flex;
                            justify-content: space-between;
                            i{
                                color: #888888
                            }
                        }
                    }
                    
                }
            }
        }
        .item-area{
            position: absolute;
            left: 90px;
            right: 0px;
            bottom: 0px;
            top: 0px;
            z-index: 1000;
            background-color: #ffffff;
            > ul{
                padding: 0 27px;
