<template>
    <div class="page-index" v-if="data">
        <div class="" v-if="!areaStatus">
            <van-nav-bar            
                left-arrow
                title="编辑个人资料"
                @click-left="onClickLeft"
            >
            
            </van-nav-bar>
            <section class="main">
                <div class="avatar-box">
                    <van-uploader    :after-read="afterRead"   >
                        <img :src="data.avatar" >
                        <p>点击更换头像</p>
                    </van-uploader>
                </div>
                <ul class="list">
                    <li class="item">
                        <p class="label">昵称</p><p class="value" @click="handleToEdit('1')">{{data.username}}<van-icon name="arrow" /></p>
                    </li>
                    <li class="item">
                        <p class="label">签名</p><p class="value" v-bind:class="{'not-value':!data.bio}" @click="handleToEdit('2')">{{data.bio ?  data.bio:'填写个性签名更容易获得关注'}}<van-icon name="arrow" /></p>
                    </li>
                    <li class="item">
                        <p class="label">性别</p><p class="value" v-bind:class="{'not-value':!data.gender}"  @click="handleToEdit('3')">{{data.gender ? data.gender : '点击设置' }}<van-icon name="arrow" /></p>
                    </li>
                    <li class="item">
                        <p class="label">地区</p><p class="value" v-bind:class="{'not-value':!data.resideprovince && !data.residecity}" @click="handleToEdit('4')" >{{data.resideprovince}}.{{data.residecity}} {{!data.resideprovince && !data.residecity ? '点击设置' : ''}}<van-icon name="arrow" /></p>
                    </li>
                    <li class="item">
                        <p class="label">车友圈</p><p class="value" ><van-icon name="arrow" /></p>
                    </li>
                </ul>
            </section>
            <van-action-sheet
                v-model="show"
                :actions="actions"
                cancel-text="取消"
                @select="onSelect"
                @cancel="onCancel"
            />
        </div>
         <Selectarea :areaStatus="areaStatus" @goAreaBack="goAreaBack"  @getuserinfo="getUserInfo" />
         <div class="avatar-update-box" v-if="avatarBase64">
             <div class="spherical">
                 <img :src="avatarBase64" >
             </div>
             <div class="action">
                 <div class="flex justify-space-between">
                    <span @click="handleCancel">取消</span>
                    <span @click="handleOk">确认</span>
                 </div>
             </div>
         </div>
    </div>
   
</template>

<script>
import { Toast } from 'vant';
import Selectarea from './selectarea'
export default {
    components:{
        Selectarea,
    },
    data(){
        return {
            show:false,
            areaStatus:false,
            avatarBase64:'',
            avatarFile:'',
            actions: [
                { name: '男', color: '#696D7D' ,value:1},
                { name: '女', color: '#696D7D' ,value:2},
                { name: '不显示', color: '#696D7D' ,value:0},
            ],
            data:''
        }   
    },
    created(){
        this.getUserInfo();

    },
    methods:{
        // 获取 用户信息
        getUserInfo(){
            this.areaStatus = false;
            this.api.getUserInfo({
                token:this.$store.state.userData,
                uid:''
            }).then( res => {

                if(res.err_code == 0){
                    this.data = res.data;
                   
                }
            })
        },
        // 取消上传头像
        handleCancel(){
            this.avatarBase64 = '';
            this.avatarFile = '';
        },
        // 确认上传头像
        handleOk(){
            this.postImage(this.avatarFile);
        },
        // 上传头像
        postImage(file){
            Toast.loading({
                message: '加载中...',
                forbidClick: true
            });
            let formData = new FormData();
            formData.append('token',this.$store.state.userData)
            formData.append('myfile',file)
            this.api.saveMyAvatar(formData).then(res => {
                Toast.clear();
                if(res.err_code == 0){
                    this.avatarBase64 = '';
                    this.avatarFile = '';
                    this.getUserInfo();
                }else {
                    console.log(res);
                }
            }).catch(err => {
                Toast(err)
            })
        },
        afterRead(file){
            console.log(file);
            this.avatarBase64 = file.content
            this.avatarFile = file.file;
          // this.postImage(file.file);
        }, 

        // 选择地区返回
        goAreaBack(v){
            this.areaStatus = v;
        },
        handleToEdit(v){
            switch(v){
                case '1':
                    this.$router.push({ path: '/my/editusername' })
                    break;
                case '2':
                    this.$router.push({ path: '/my/editsignature' })
                    break;    
                case '3':
                    this.show = true;
                    break;    
                case '4':
                    this.areaStatus = true;
                    break;
            }
        },
        saveMyInfoByGender(val){
            Toast.loading({
                message: '加载中...',
                forbidClick: true
            });
            this.api.saveMyInfoByGender({
                token:this.$store.state.userData,
                gender:val
            }).then(res => {
                Toast.clear();
                this.show = false;
                if(res.err_code == 0){
                    this.getUserInfo();
                }else {
                    return Toast(res.err_msg);
                }
            })
        },
        onCancel(){

        },
        onSelect(v){
            this.saveMyInfoByGender(v.value);
        },
        onClickLeft(){
            this.$router.go(-1)
        },

    }
}
</script>

